    01.04 extend css 
=============================== */ 
/* ======== Font Size For responsive devices Start ======== */
.fs-10 {
  @include fs-10;
}
.fs-11 {
  @include fs-11;
}
.fs-12 {
  @include fs-12;
}
.fs-13 {
  @include fs-13;
}
.fs-14 {
  @include fs-14;
}
.fs-15 {
  @include fs-15;
}
.fs-16 {
  @include fs-16;
  @include lg-screen {
    @include fs-15;
  }
}
.fs-17 {
  @include fs-17;
  @include lg-screen {
    @include fs-16;
  }
  @include sm-screen {
    @include fs-15;
  }
}
.fs-18 {
  @include fs-18;
  @include xl-screen {
    @include fs-17;
  }
  @include sm-screen {
    @include fs-16;
  }
}
.fs-20 {
  @include fs-20;
  @include xl-screen {
    @include fs-18;
  }
  @include sm-screen {
    @include fs-17;
  }
} 


.section-title{ 
  h2,.h2{
    font-size: rem(48px);
    line-height: 1.2;
    font-weight: 700;
  }
  span{
      padding-bottom: 15px;
  }
}

hr{
  color: var(--gray5);
  margin: 24px 0;
}
